export function formattedDateForComp(date) {
    const localDate = new Date(date);
    // Extract date components
    const year = localDate.getFullYear();
    const month = (localDate.getMonth() + 1).toString().padStart(2, '0'); // Month is zero-based
    const day = localDate.getDate().toString().padStart(2, '0');
    const hours = localDate.getHours().toString().padStart(2, '0');
    const minutes = localDate.getMinutes().toString().padStart(2, '0');

    // Format the date as "YYYY-MM-DDTHH:mm"
    const formattedDate = `${year}-${month}-${day}T${hours}:${minutes}`;
    return formattedDate;
};


export function amountInWords(amount) {
    const units = ['', 'One', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight', 'Nine'];
    const teens = ['Ten', 'Eleven', 'Twelve', 'Thirteen', 'Fourteen', 'Fifteen', 'Sixteen', 'Seventeen', 'Eighteen', 'Nineteen'];
    const tens = ['', '', 'Twenty', 'Thirty', 'Forty', 'Fifty', 'Sixty', 'Seventy', 'Eighty', 'Ninety'];

    function convertToWords(num) {
        if (num === 0) return '';
        if (num < 10) return units[num];
        if (num < 20) return teens[num - 10];
        return `${tens[Math.floor(num / 10)]} ${units[num % 10]}`;
    }



    function convertToWordsWithScale(num, scale) {
        return num === 0 ? '' : `${convertToWords(num)} ${scale}`;
    }


    function convertRupeesToWords(rupees) {
        return convertToWordsWithScale(Math.floor(rupees / 10000000), 'Crore ') +
            convertToWordsWithScale(Math.floor((rupees % 10000000) / 100000), 'Lakh ') +
            convertToWordsWithScale(Math.floor((rupees % 100000) / 1000), 'Thousand ') +
            convertToWordsWithScale(Math.floor((rupees % 1000) / 100), 'Hundred ') +
            convertToWords(rupees % 100);
    };

    function convertPaiseToWords(paise) {
        return convertToWords(paise);
    };

    const parts = amount.toString().split('.');
    const rupees = parseInt(parts[0], 10);
    const paise = parts[1] ? parseInt(parts[1], 10) : 0;

    const rupeesInWords = convertRupeesToWords(rupees);
    const paiseInWords = convertPaiseToWords(paise);

    const result = `${rupeesInWords} Rupees${paise > 0 ? ` and ${paiseInWords} Paise` : ''} Only.`;
    return result.trim();
};
