import { lazy, Suspense } from 'react';
import { Outlet } from 'react-router-dom';
// auth
import { AuthGuard } from 'src/auth/guard';
// layouts
import DashboardLayout from 'src/layouts/dashboard';
// components
import { LoadingScreen } from 'src/components/loading-screen';

// ----------------------------------------------------------------------

const OverviewAppView = lazy(() => import('src/sections/overview/app/view/overview-app-view'));
const IndexPage = lazy(() => import('src/pages/dashboard/one'));
const PageTwo = lazy(() => import('src/pages/dashboard/two'));
const PageThree = lazy(() => import('src/pages/dashboard/three'));
const PageFour = lazy(() => import('src/pages/dashboard/four'));
const PageFive = lazy(() => import('src/pages/dashboard/five'));
const PageSix = lazy(() => import('src/pages/dashboard/six'));
const JCCreatePage = lazy(() => import('src/pages/dashboard/jcPage'));
const JCUpdatePage = lazy(() => import('src/pages/dashboard/jcUpdatePage'));

// ----------------------------------------------------------------------

export const dashboardRoutes = [
  {
    path: 'dashboard',
    element: (
      <AuthGuard>
        <DashboardLayout>
          <Suspense fallback={<LoadingScreen />}>
            <Outlet />
          </Suspense>
        </DashboardLayout>
      </AuthGuard>
    ),
    children: [
      { element: <OverviewAppView />, index: true },
      { path: 'lead_management', element: <IndexPage /> },
      { path: 'jobcard', element: <PageTwo /> },
      { path: 'create_jobcard', element: <JCCreatePage /> },
      { path: 'update_jobcard', element: <JCUpdatePage /> },
      { path: 'three', element: <PageThree /> },
      // {
      // path: 'group',
      // children: [
      // { element: <PageFour />, index: true },
      { path: 'four', element: <PageFour /> },
      { path: 'five', element: <PageFive /> },
      { path: 'six', element: <PageSix /> },
      // ],
      // },
    ],
  },
];
